import { spawn } from 'child_process'
import { createWriteStream as stream } from 'fs'
import { pick, omit } from 'lodash'

export function shell(options = {}){
  let { data } = options

  let log = options.log ? stream(options.log) : undefined

  let proc = spawn(cmd, args, pick(options, 'cwd', 'env', 'input', 'output'))

  const handleData = (buffer) => {
    let content = buffer.toString()
    data(content)
    log && log.write(content)
  }

  proc.stdout.on('data', handleData)
  proc.stderr.on('data', handleData)

  return proc
}



